<template>
  <div class="card">
    <div :class="{ amarillo: documento.data.descuento > 0 }">
      <h2>Folio: {{ documento.id }}</h2>
      <div class="espacio-en-medio">
        <div class="cardi">
          <h3>Vehculo</h3>
          <p>
            <span
              >{{ documento.data.caract_veh[2] }} -
              {{ documento.data.caract_veh[0] }} -
              {{ documento.data.caract_veh[1] }}</span
            >
          </p>
          <p>
            <span>{{ documento.data.caract_veh[4] }}</span>
          </p>
        </div>

        <div class="cardi">
          <h3>Servicios:</h3>
          <p>
            <span>Equipo: {{ documento.data.caract_veh[3] }}</span>
          </p>
          <p v-for="(servicio, index) in documento.data.servicios" :key="index">
            <span>
              {{ servicio }}
            </span>
          </p>
        </div>

        <div class="cardi" v-if="fechaIngresada">
          <h3>Ingreso</h3>
          <p>
            <span>Fecha: {{ fechaFormateada }}</span>
          </p>
          <p>
            <span> Hace: {{ tiempoTranscurrido }} </span>
          </p>
          <p>
            <span>Usuario: {{ documento.data.usuario_ingreso }}</span>
          </p>
        </div>
        <div class="cardi" v-else>
          <h3>Ingreso</h3>
          <span>Cargando informacin...</span>
        </div>

        <div class="cardi">
          <h3>Costos</h3>
          <p>
            <span>Total: ${{ documento.data.total }}</span>
          </p>
          <p>
            <span>Descuento: ${{ documento.data.descuento }}</span>
          </p>
          <p>
            <span>Pagado: ${{ documento.data.pagado }}</span>
          </p>
        </div>

        <div class="cardi">
          <h3>Entrega</h3>
          <p>
            <span>Entregado: {{ documento.data.ya_entregado }}</span>
          </p>
          <p>
            <span>Usuario: {{ documento.data.usuario_entrego }}</span>
          </p>
          <p>
            <span v-if="this.documento.data.fecha_entrega"
              >Fecha:
              {{
                new Date(
                  this.documento.data.fecha_entrega.seconds * 1000
                ).toLocaleString()
              }}</span
            >
          </p>
        </div>

        <div class="cardi">
          <h3>Mtodo de pago</h3>
          <p>
            <span>Mtodo: {{ documento.data.medio_pago }}</span>
          </p>
          <p>
            <span>Usuario: {{ documento.data.usuario_pagaron }}</span>
          </p>
          <p>
            <span v-if="this.documento.data.fecha_pago"
              >Fecha:
              {{
                new Date(
                  this.documento.data.fecha_pago.seconds * 1000
                ).toLocaleString()
              }}</span
            >
          </p>
        </div>

        <div class="cardi">
          <h3>Cliente</h3>
          <p>
            <span>Nombre: {{ documento.data.nombre }}</span>
          </p>
          <p>
            <span>Telfono: {{ documento.data.cliente }}</span>
          </p>
        </div>

        <div class="cardi">
          <h3>Da del mes en el que se encontr:</h3>
          <span>{{ documento.mes }}</span>
        </div>
      </div>

      <!--Botones correspondientes con el estatus del carro-->
      <form class="f">
        <div class="horizontal">
          <!--Botn para imprimir el ticket de ingreso-->
          <button
            class="btn button btn-primary"
            type="submit"
            @click="imprimirPDF(documento)"
          >
            <strong>
              <i class="fa fa-print espacio-der"></i>
              Imprimir ticket
            </strong>
          </button>
          <!--Botn para imprimir el ticket de pago-->
          <button
            class="btn button btn-primary"
            type="submit"
            @click="imprimirPago(documento)"
            v-if="mostrarBoton == 0"
          >
            <strong>
              <i class="fa fa-credit-card espacio-der"></i>
              Imprimir ltimo pago
            </strong>
          </button>
          <!--Botn para marcar al vehculo como listo-->
          <button
            class="btn button btn-primary"
            type="submit"
            @click="actualizar_listo(documento.id, documento.mes)"
            v-if="mostrarBoton == 1"
          >
            <strong>
              <i class="fa fa-check espacio-der"></i>
              Actualizar estatus a vehculo listo
            </strong>
          </button>
          <!--Botn para marcar al vehculo como entregado-->
          <button
            class="btn button btn-primary"
            type="submit"
            @click="
              actualizar_entregado(
                documento.id,
                documento.mes,
                documento.data.pagado,
                documento.data.descuento,
                documento.data.total
              )
            "
            v-if="mostrarBoton == 2"
          >
            <strong>
              <i class="fa fa-car espacio-der"></i>
              Entregar vehculo
            </strong>
          </button>
        </div>

        <!--Botn para enviar mensaje al cliente-->
        <button
          class="btn button btn-primary"
          type="submit"
          @click="sendWhatsAppMessage(documento.data.cliente)"
          v-if="mostrarBoton == 2"
          :disabled="!esTelefonoValido(documento.data.cliente)"
        >
          <strong>
            <i class="fa fa-phone espacio-der"></i>
            Enviar mensaje al cliente
          </strong>
        </button>
      </form>
    </div>
  </div>

  <!--Imprimir-->
  <imprimir-component :show="showPrint" :data="data" @close="closePrint">
  </imprimir-component>

  <!--Imprimir el comprobante de pago-->
  <imprimir-pago
    :show="showPrintP"
    :data="dataP"
    @close="closePrintP"
  ></imprimir-pago>
</template>

<script>
//Importar componentes
import ImprimirComponent from "../print/Imprimir.vue";
import ImprimirPago from "../print/ImprimirPago.vue";

export default {
  props: {
    documento: Object,
    mostrarBoton: String,
  },
  components: {
    ImprimirComponent,
    ImprimirPago,
  },
  data() {
    return {
      //Imprimir ingreso
      showPrint: false,
      data: null,
      //Imprimir pago
      showPrintP: false,
      dataP: null,
    };
  },
  methods: {
    // Valida que el nmero de telfono del cliente sea vlido
    esTelefonoValido(numero) {
      return /^\d{10}$/.test(numero);
    },
    // Enva los parmetros para actualizar el estatus del vahiculo
    actualizar_listo(id, mes) {
      this.$emit("actualizar", { id, mes });
    },

    // Enva los parmetros para actualizar el estatus del vahiculo
    actualizar_entregado(id, mes, pagado, descuento, total) {
      const dado = pagado + descuento;
      this.$emit("entregar", { id, mes, dado, total });
    },

    //Enva un mensaje al cliente
    sendWhatsAppMessage(number) {
      const completo = "521" + number;
      const message =
        "Hola! Tu vehculo ya est listo, y ya puedes pasar por l.";
      const encodedMessage = encodeURIComponent(message);
      const whatsappUrl = `https://wa.me/${completo}?text=${encodedMessage}`;

      // Abrir en nueva pestaa
      window.open(whatsappUrl, "_blank");
    },

    // Imprimir ingreso
    imprimirPDF(doc) {
      this.data = {
        id: doc.id,
        tipo: doc.data.caract_veh[1],
        modelo: doc.data.caract_veh[2],
        placas: doc.data.caract_veh[0],
        equipo: doc.data.caract_veh[3],
        fecha: new Date(doc.data.fecha_ingresado.seconds * 1000),
        tel: doc.data.cliente,
        servicios: doc.data.servicios,
        precios: doc.data.precios,
        descuento: doc.data.descuento,
        total: doc.data.total,
      };
      this.showPrint = true;
    },
    closePrint() {
      this.showPrint = false;
    },

    //Imprimir pago
    imprimirPago(doc) {
      this.dataP = {
        fecha: new Date(doc.data.fecha_pago.seconds * 1000),
        cajero: doc.data.usuario_pagaron,
        id: doc.id,
        modelo: doc.data.caract_veh[2],
        tp: doc.data.medio_pago,
        total: doc.data.total - doc.data.descuento,
        pagado: doc.data.pagado,
        restante: doc.data.total - doc.data.pagado - doc.data.descuento,
        tel: doc.data.cliente ? doc.data.cliente : "",
      };
      this.showPrintP = true;
    },
    closePrintP() {
      this.showPrintP = false;
    },
  },
  computed: {
    fechaIngresada() {
      return new Date(this.documento.data.fecha_ingresado.seconds * 1000);
    },
    fechaFormateada() {
      return this.fechaIngresada.toLocaleString(); // Formato legible
    },
    tiempoTranscurrido() {
      const ahora = new Date();
      const diferenciaMs = ahora - this.fechaIngresada; // Diferencia en milisegundos
      const minutos = Math.floor(diferenciaMs / (1000 * 60)) % 60;
      const horas = Math.floor(diferenciaMs / (1000 * 60 * 60));

      return `${horas} horas y ${minutos} minutos`;
    },
  },
};
</script>
